<template>
  <li class="catalog__item">

    <router-link
      class="catalog__pic"
      :to="{name: 'product', params: {id: product.id}}"
    >
      <img :src="product.image" :alt="product.title ">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ price | numberFormat }} 
    </span>

    <ProductBtns
      :parentIndex = parentIndex
      :offerValue="offerValue"
      :activeOffer="activeOffer"
      :mainProp="mainProp"
      :colorCode="colorCode"
      @check="activeOffer = $event"
    />

  </li>
</template>

<script>
import ProductBtns from '@/components/product/ProductBtns.vue';
import numberFormat from '@/helpers/numberFormat';

export default {
  name: 'ProductItem',
  components: { ProductBtns },
  props: ['product', 'parentIndex'],
  data() {
    return {
      activeOffer: 0,
    };
  },
  filters: { numberFormat },
  computed: {
    //  
    offers() {
      return this.product.offers;
    },
    //   
    mainProp() {
      return this.product.mainProp.id;
    },
    //  
    //   ,     ..
    offerValue() {
      const arr = [];
      this.offers.forEach((el) => {
        el.propValues.forEach((elem) => {
          const { value } = elem;
          arr.push(value);
        });
      });
      return arr;
    },
    //    
    price() {
      return this.offers[this.activeOffer].price;
    },
    //   
    colorCode() {
      //   ,   API ...
      //     (     - . )
      if (this.mainProp === 7) {
        // .      
        const arr = [];
        this.product.colors.forEach((el) => {
          //     
          const title = this.replaceSimbols('', '', el.color.title);
          arr.push({ title, code: el.color.code });
        });

        //     -   
        const newArr = [];
        this.offerValue.forEach((el) => {
          const index = arr.findIndex((n) => {
            const title = this.replaceSimbols('', '', n.title);
            return title === el;
          });
          newArr.push(arr[index].code);
        });
        return newArr;
      } return null;
    //             -   id:6
    //       API
    },
  },
  methods: {
    replaceSimbols(a, b, str) {
      return str.replace(RegExp(`${a}`, 'gi'), `${b}`);
    },
  },
};
</script>

<style scoped lang="sass">
  .catalog__pic img
    object-fit: contain
</style>
