//         
export default function xCrement(value, bool) {
  // console.log(`: ${value}`);
  let newValue = value;
  //  bool === true,    1
  if (bool) {
    newValue = value + 1;
    // console.log(`: ${newValue}`);
    return newValue;
  }
  //  bool === false  value === 1,    
  if (value === 1) {
    // console.log(`!== 1: ${newValue}`);
    return value;
  }
  //  bool === false  value !== 1,     1
  newValue = value - 1;
  // console.log(`: ${newValue}`);
  return newValue;
}
