<template>
  <div>

    <!--     ,   -->
    <PreLoader v-show="productsLoading"/>

    <main class="content container">
      <div class="content__top content__top--catalog">
        <h1 class="content__title"></h1>
        <span class="content__info">{{ countProducts }} </span>
      </div>

      <div class="content__catalog">

        <!--     -->
        <ProductFilter
          :price-from.sync="filterPriceFrom"
          :price-to.sync="filterPriceTo"
          :category-id.sync="filterCategoryId"
          :categoryMainPropSlug.sync='filterCategoryMainPropSlug'
          :sku.sync="filterSku"
          @loadProductsByFilters="loadProducts"
          @resetFilters="resetFilters"
        />

        <section class="catalog">

          <!--        -->
          <div v-if="productsLoadingFailed">
                <br>
            <button @click.prevent="loadProducts">  </button>
          </div>

          <!--     -->
          <ProductList
            :products="products"
          />

          <!--    -->
          <BasePagination
            :page.sync="page"
            :count="countProducts"
            :per-page="productsPerPage"
          />

          <!--       -->
          <div class="catalog__productsPerPage-wrap" v-show="countProducts !== 0">
            <span>   </span>
            <label for="productsPerPage">
              <select
                id="productsPerPage"
                name="productsPerPage"
                type="text"
                v-model="productsPerPage"
              >
                <option value="3">3</option>
                <option value="9">9</option>
                <option value="12">12</option>
                <option value="18">18</option>
                <option value="24">24</option>
                <option value="27">27</option>
                <option value="32">32</option>
              </select>
            </label>
          </div>

        </section>

      </div>
    </main>

  </div>
</template>

<script>
import axios from 'axios';
import PreLoader from '@/components/PreLoader.vue';
import ProductFilter from '@/components/filter/ProductFilter.vue';
import ProductList from '@/components/product/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import API_BASE_URL from '../config';

export default {
  name: 'MainPage',
  components: {
    ProductFilter, ProductList, BasePagination, PreLoader,
  },
  data() {
    return {
      productsLoading: false,
      productsLoadingFailed: false,

      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,

      filterCategoryMainPropSlug: '',
      filterSku: [],

      page: 1,
      productsPerPage: 12,

      productsData: null,
    };
  },
  //       
  props: ['categoryId'],
  computed: {
    //  
    products() {
      return this.productsData
        //  this.productsData === true,   
        ? this.productsData.items.map((prod) => ({
          ...prod,
          //    
          image: prod.preview.file.url,
        }))
        //  this.productsData === false,    
        : [];
    },
    //  
    countProducts() {
      //  this.productsData === true,     ,
      //  this.productsData === false,   0
      return this.productsData ? this.productsData.pagination.total : 0;
    },
    //   (    )
    inputCategoryId() {
      return this.categoryId ? this.categoryId : null;
    },
  },
  methods: {
    //  .    
    loadProducts() {
      //  
      this.productsLoading = true;
      //   
      this.productsLoadingFailed = false;

      //   
      const params = {};
      //    = true,  ,     
      if (this.filterPriceFrom) params.minPrice = this.filterPriceFrom;
      if (this.filterPriceTo) params.maxPrice = this.filterPriceTo;
      if (this.filterCategoryId) params.categoryId = this.filterCategoryId;

      //  ,    query 
      let str = '';
      //    
      if (this.filterSku.length !== 0) {
        this.filterSku.forEach((el, i) => {
          str += `props[${this.filterCategoryMainPropSlug}][]=${el}`;
          //  ,      
          //      -  
          if (i < this.filterSku.length - 1) str += '&';
        });
      }

      //  
      clearTimeout(this.loadProductsTimer);
      //    setTimeout,      
      //     ,   .    .
      // this.loadProductsTimer -   - ,    ,
      //   - .
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products?${str}`, {
            // ,     query-string
            //     
            params: {
              page: this.page,
              limit: this.productsPerPage,
              ...params,
            },
          })
          .then((response) => {
            this.productsData = response.data;
            // console.log(response.data.items[0].);
          })
          //   
          .catch(() => {
            this.productsLoadingFailed = true;
          })
          //   (  )    
          //       
          .then(() => {
            this.productsLoading = false;
          });
      }, 0);
    },
    //   
    resetFilters() {
      this.filterPriceFrom = 0;
      this.filterPriceTo = 0;
      this.filterCategoryId = 0;
      this.filterCategoryMainPropSlug = '';
      this.filterSku = [];
      this.loadProducts();
    },
  },
  watch: {
    //    
    page() {
      this.loadProducts();
    },
    //        
    productsPerPage() {
      this.loadProducts();
    },
  },
  created() {
    if (this.inputCategoryId) {
      this.filterCategoryId = this.inputCategoryId;
    }
    this.loadProducts();
  },
};
</script>
