<template>
  <main class="content container">

    <!--   -->
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title">
          <span> {{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  .
            &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>

            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.deliveryType.title }}&nbsp;
                ({{ orderInfo.deliveryType.price | numberFormat }}&nbsp;)
              </span>
            </li>

            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.paymentType }}
              </span>
            </li>
          </ul>
        </div>

        <OrderList
          :products="orderInfo.basket.items"
          :totalPrice="Number(orderInfo.totalPrice)"
          :totalProducts="totalProducts"
          :delivery="orderInfo.deliveryType.price"
        />

      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import OrderList from '@/components/order/OrderList.vue';

export default {
  name: 'OrderInfoPage',
  components: { OrderList },
  filters: { numberFormat },
  computed: {
    //      state
    orderInfo() {
      return this.$store.getters.orderInfo ? this.$store.getters.orderInfo : {};
    },
    totalProducts() {
      return this.orderInfo.basket.items.reduce((acc, e) => e.quantity + acc, 0);
    },
  },
  created() {
    // ,     () 
    //      id   state === id "  "
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      return;
    }
    //      
    //    ,
    //           state
    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
};
</script>
